import React, { Component } from 'react';
import {Doughnut} from 'react-chartjs-2';
import {asset, equity} from './dataset'
import Grid from './components/Grid'
import './App.css';

class App extends Component {
  render() {  
    return (
      <div className="App">          
         <section className="row" id="report-controls">
           <div className="col-sm-12 col-md-6 col-md-offset-6">
           <div>
              <div className="pull-right">
                <button className="btn btn-primary">Export <i className="glyphicon glyphicon-triangle-bottom"></i></button>
                {" "}<button className="btn btn-primary">Print</button>
                {" "}<button className="btn btn-primary">Select Date <i className="glyphicon glyphicon-triangle-bottom"></i></button>
              </div>
            </div>
           </div>
         </section> 
         <section className="row" id="report-header">
           <div className="column col-md-6 col-lg-6">
             <div className="pull-left" id="client-details">
                <h3>Valuation</h3>
                <div id="client-name">John Client - 2345678 ($CAD)</div>
                <div id="held-at">Held At: State Street Trust Company Canada as Custodian</div>
             </div>
           </div>          
           <div className="column col-md-6 col-lg-6">
              <div id="report-date">
                 <div className="as-of">
                   <h4>As of: November 6, 2017</h4>
                 </div>
               </div>
           </div>
         </section>
         <section className="row" id="charts">
            <div className="col-md-12 col-lg-12">
              <div className="row">
                <ul className="menu">
                  <li>
                    <a href="#">
                      <i className="menu-item valuation"></i>
                    </a>
                    Valuation
                  </li>
                  <li>
                    <a href="#">
                      <i className="menu-item performance"></i>
                    </a>
                    Performance
                  </li>
                  <li>
                    <a href="#">
                      <i className="menu-item investment"></i>
                    </a>
                    Investment
                  </li>
                  <li>
                    <a href="#">
                      <i className="menu-item transaction"></i>
                    </a>
                    Transaction
                  </li>
                </ul>
              </div>
              <div className="row">
                <div className="col-sm-12 col-md-12 col-lg-6">
                  <Doughnut data={asset} options={ asset.options }/>
                </div>
                <div className="col-sm-12 col-md-12 col-lg-6">
                  <Doughnut data={equity} options={ equity.options }/>
                </div>
              </div>
            </div>           
         </section>
         <br />
         <Grid />
      </div>     
    );
  }
}

export default App;



