import React from 'react';
import {report} from '../dataset'

const Grid = function () {
    return (
        <section>
            <table className="security-table table">
                <thead>
                    <tr>
                        <th>                        
                         <button className="btn btn-default btn-xs toggle-btn">
                            <span className="glyphicon glyphicon-minus"></span>
                         </button>{" "}
                        Security{" "}
                        <i class="fa fa-sort"></i>
                        </th>
                        <th>Quality</th>
                        <th>Unit Cost</th>
                        <th>Adjusted Cost *</th>
                        <th>Market <br/> Price</th>
                        <th>Market Value{" "}<i class="fa fa-sort"></i></th>
                        <th>Market Yied</th>
                        <th>Estimated <br />Annual Income</th>                        
                        <th>Weight</th>
                    </tr>
                </thead>
                <tbody>                    
                    { report.map(function(value, index) {
                        var className = (value.level == 2 ? "level-2" : (value.level == 3 ? "level-3" : ""));
                        className += value.highlight ? " highlight" : "";

                        return (
                            <tr className={ className }>
                                <td>
                                    {value.label.split('\n').map((item) => <div>{item}</div>)}
                                </td>
                                {
                                    value.values ? value.values.map((value) => {
                                        return <td>{ value }</td>
                                    })
                                    : null
                                }
                            </tr>
                        )
                    }) }
                </tbody>
            </table>
        </section>
    )
}

export default Grid;


